.backdrop-auth {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 101;
  width: 100%;
  height: 100%;

  padding-top: auto;
  padding-bottom: auto;

  background: rgba(0, 0, 0, 0.8);

  opacity: 1;
  visibility: visible;
  transition: opacity 300ms cubic-bezier(0.38, 0.53, 0.8, 0.71),
    visibility 300ms cubic-bezier(0.38, 0.53, 0.8, 0.71);
}

.visibility {
  opacity: 0;
  visibility: hidden;
  // pointer-events: none;
}

.auth-container {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 400px;
  pointer-events: all;
  padding: 40px;
  transform: translate(-50%, -50%);
  background: rgb(19, 19, 19);
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 1);
  border-radius: 5px;
  width: 310px;
  @media screen and (min-width: 480px) {
    width: 390px;
  }
}

.auth-container .hidden-tab {
  transform: translate(-50%, -50%) scale(0.2);
}

.tabs {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px;
  border-radius: 5px;

  // background: #03e8f4c5;
  background: rgba(255, 255, 255, 0.1);
  opacity: 1;
  visibility: visible;
  pointer-events: none;
  font-family: sans-serif;
  transition: opacity 300ms cubic-bezier(0.38, 0.53, 0.8, 0.71),
    visibility 300ms cubic-bezier(0.38, 0.53, 0.8, 0.71);
}

.tabs-list {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
  height: 50px;
  font-size: 16px;
  opacity: 1;
  visibility: visible;
  color: var(--accent-color);
  border-bottom: 2px solid var(--accent-color);
  // @media screen and (min-width: 480px) {
  //   margin-right: 10px;
  // }
  &:hover,
  &:focus {
    cursor: pointer;
    color: var(--accent-color);
  }
}
.tabs-list-login {
  justify-content: baseline;
}
