.confirm-btn span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #03e9f4);
  animation: btn-anim2 1s linear infinite;
  animation-delay: 0.25s;
}
@keyframes btn-anim2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}
.confirm-btn span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #03e9f4);
  animation: btn-anim3 1s linear infinite;
  animation-delay: 0.5s;
}

@keyframes btn-anim3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}
.confirm-btn span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #03e9f4);
  animation: btn-anim4 1s linear infinite;
  animation-delay: 0.75s;
}

@keyframes btn-anim4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}
.del-msg {
  font-size: 25px;
  text-align: center;
  margin-bottom: 30px;

  opacity: 1;
  visibility: visible;

  transition: opacity 300ms cubic-bezier(0.38, 0.53, 0.8, 0.71),
    visibility 300ms cubic-bezier(0.38, 0.53, 0.8, 0.71);
}

.hidden-tab {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;

  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.login-eye-position.show-pswd,
.upd-eye-position.show-pswd,
.reg-eye-position.show-pswd {
  // fill: rgba(124, 124, 124, 0.5);
  padding: 0;
  margin: 0;
}

.login-eye-position.hide-pswd,
.upd-eye-position.hide-pswd,
.reg-eye-position.hide-pswd {
  fill: var(--accent-color);
  padding: 0;
  margin: 0;
}

.login-eye-position {
  position: absolute;
  top: 200px;
  right: 49px;
  cursor: pointer;
}

.reg-eye-position,
.upd-eye-position {
  position: absolute;
  top: 270px;
  right: 49px;
